import './App.css';
import React from 'react';
import { useEffect } from 'react';
import Granim from "granim";
import logo from './logo.webp';

function App() {
  useEffect(() => {
    new Granim ({
      element: "#canvas",
      direction: "radial",
      opacity: [1, 1],
      states: {
        "default-state": {
          gradients: [
            ["#7C3BBE", "#3BBE7C"],
            ["#3BBE7C", "#BE7C3B"],
            ["#BE7C3B", "#7C3BBE"],
          ],
          transitionSpeed: 1000
        }
      }
    });
  }, []);

  return (
    <div className="App">
      <canvas id="canvas" className="canvas"></canvas>
      <img className="logo-mask" src={logo} alt="logo" />
      <div className="App-text-content">
        <header className="App-title">ChronoSphere</header>
        <p className="App-subtitle">Bringing Tomorrow to You, Today!</p>
      </div>
    </div>
  );
}

export default App;
